<template>
  <div class="art">
    <div class="art-body">
      <h2 class="art-title">
        <a @click="tz(art.id)">{{art.title}}</a>
      </h2>
      <div class="art-content" v-html="art.content"></div>
    </div>
    <div class="art-foot">发布于 {{ art.ctime }} | 浏览 ( {{art.views}} ) | Tags：{{art.tags}}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    art: {
      id: {
        type: String,
        required: true,
      },
      title: String,
      ctime: String,
      content: String,
      vies: Number,
      tags: Array,
    },
  },
  computed: {
    ...mapState(['artId']),
  },
  methods: {
    /** 跳转 */
    tz(id) {
      this.$store.commit('setArtId', { id });
      this.$router.push(`/home/art/${id}`);
    },
  },
};
</script>

