  /* box-shadow: 0 0 3px rgba(97, 97, 97, .8); */
  background-color: #fff;
  border-radius: 5px;
  border-color: #bfbfbf #8e8e8e #5f5f5f #bfbfbf;
  a{
    font-size: 20px;
    color:#13102b;
    text-decoration: none;
  }
  .art-body {
    padding: 15px 15px 15px 15px;
    margin-bottom: 15px;

    & > p {
      margin-right: 20px;
      line-height: 1.5;
      font-size: 14px;
    }
    .art-content{
      color: #555;
    }
  }
  .art-title {
