<template>
       <div class="sub-comment">
      <div class="headP">
        <span>
          <i class="el-icon-s-custom"></i>
        </span>
      </div>
      <div class="cbody">
        <div class="auther">
          <span class="name">{{reply.user_name}} 回复 {{reply.replyInfo}}</span>
          <span class="public">发表于{{reply.utime}}</span>
          <span class="reply">
            <a  v-on:click="addCF(reply.user_name)">[回复]</a>
          </span>
        </div>
        <div class="comment-body" v-html="reply.comments">
        </div>
      </div>
      <input :ref="'cid'+reply.id" type="text" />
    </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    reply: Object,
    addCF: Function,
  },
  computed: {
    ...mapState(['cId']),
  },
  methods: {
    getFous(cid) {
      const cc = this.$refs[`cid${cid}`];
      if (cc !== 'undefined' && cc) {
        cc.focus();
      }
    },
  },
  watch: {
    /** 如果点击了最新评论获取焦点 */
    cId(newV) {
      this.$nextTick(() => {
        const cc = this.$refs[`cid${newV.cid}`];
        if (cc !== 'undefined' && cc) {
          cc.focus();
        }
      });
    },
  },
  mounted() {
    /** 如果点击了最新评论获取焦点 */
    this.$nextTick(() => {
      this.getFous(this.cId.cid);
      this.$store.commit('setCId', { cid: null });
    });
  },
};
</script>

<style lang="scss" scoped>
input{ // 如果点击了最新评论获取焦点的 载体
  position:absolute;
  display:inline-block;
  width: 0;
  left:-10px;
}
.sub-comment {
  overflow: hidden;
  width: calc(100% - 40);
  border-radius: 5px;
  // background-color: rgba(139, 233, 238, 0.3); // #cbe7eb;
  // box-shadow: 0 0 8px rgba(119, 203, 218, 0.5);
  padding: 5px 5px;
  // margin: 0px 0;
  display: flex;
  border-bottom: 1px solid rgba($color: #ccc, $alpha: 1.0);
  .headP {
    width: 35px;
    span {
      display: inline-block;
      width: 35px;
      height: 35px;
      line-height: 35px;
      margin-left:-4px;
      background-color: #ddd;
      border-radius: 50%;
      text-align: center;
      i {
        display: inline-block;
        width: 30px;
        height: 30px;
      }
    }
  }
  .cbody {
    flex: 1;
    .auther {
         position: relative;
      height: 35px;
      .name {
        display: inline-block;
        height: 35px;
        line-height: 35px;
        color: #792510;
      }
      .public {
          position: absolute;
        right:60px;
        top:3px;
        display: inline-block;
        height: 35px;
        line-height: 35px;
       color: #909090;
      }
      .reply {
         position: absolute;
        margin: 0 10px;
        top:8px;
        right:0;
        a {
          text-decoration: none;
          color: #909090;
          &:hover {
           color:black;
            cursor: pointer;
          }
        }
      }
    }
    .comment-body {
      font-size: 14px;
       color: rgb(59, 73, 107);
      line-height: 22px;
    }
  }
}
</style>
