<template>
  <div class="public-comment" id="comm">
    <p>发表评论</p>
    <div class="auther-info">
      <input v-model="nickName" type="text" placeholder="昵称" />
      <input v-model="email" type="text" placeholder="邮箱" />
    </div>
    <div class="body">
<textarea ref="texta" v-model="commentBody" rows="6" :placeholder="replyId.user_name"></textarea>
    </div>
    <div class="auth-code">
      <input v-model="verificationCode" type="text" placeholder="验证码" />
      <img v-on:click="changeVCodeUrl" id="imgCaptcha" v-bind:src="vCodeUrl" alt />
    </div>
    <div class="submit-but">
      <button v-on:click="sendComment()">提交留言</button>
      <button v-on:click="clearReply()">取消回复</button>
    </div>
  </div>
</template>

<script>
import { sendComment } from '../../api';

export default {
  props: {
    bid: Number,
    replyId: {
      id: {
        type: Number,
        default: '-1',
      },
      user_name: {
        type: String,
        default: '请输入评论',
      },
      replyInfo: {
        type: String,
      },
    },
    // bid: Number, // -1代表留言\
    clearReply: Function,
    getData: Function,
  },
  data() {
    return {
      nickName: '',
      email: '',
      commentBody: '',
      verificationCode: '',
      vCodeUrl: '/api/verificationCode',
      rId: null,
    };
  },
  methods: {
    /** 改变验证码 */
    changeVCodeUrl() {
      // return function(){
      this.vCodeUrl = `/api/verificationCode?${new Date().getTime()}`;
      // }
    },
    /** 清除回复 */
    cancelReply() {
      this.rId = null;
    },
    /** 提交评论 */
    async sendComment() {
      if (!this.commentBody) {
        this.openMessage('warning', '评论不能为空');
        return;
      }
      if (!this.verificationCode) {
        this.openMessage('warning', '验证码不能为空');
        return;
      }
      const commentObj = {
        blog_id: this.bid,
        parent: this.replyId.id,
        user_name: this.nickName,
        reply_info: this.replyId.replyInfo,
        comments: this.commentBody,
        email: this.email,
        verificationCode: this.verificationCode,
      };

      const result = await sendComment(commentObj);
      if (+result.code === 0) {
        this.openMessage('success', result.msg);
        this.getData();
        this.user_name = '';
        this.commentBody = '';
        this.email = '';
        this.verificationCode = '';
        this.changeVCodeUrl();
      } else {
        this.openMessage('error', result.msg);
      }
    },
    /** element 消息组件 */
    openMessage(messageType, message) {
      this.$message({
        message,
        type: messageType, // success/warning/info/error
      });
    },
  },
  watch: {
    replyId(newR) {
      // console.log('监听', newR, oldR);
      // 然后调用focus方法
      this.$nextTick(() => {
        this.$refs.texta.focus();
        this.rid = newR;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.public-comment {
  width: calc(100% - 40);
  border-radius: 5px;
  background-color: #f5f5f5;
  box-shadow: 0 0 8px rgba(97, 97, 97, 0.3);
  padding: 20px;
  margin-top: 20px;
  .auther-info {
    padding-top: 10px;
    height: 35px;
    line-height: 35px;
    input {
      display: inline-block;
      width: 180px;
      height: 30px;
      margin-right: 20px;
      padding-left: 10px;
      // border-radius: 5px;
      // border:1px solid transparent;
      // box-shadow: 0 0 3px rgba(97, 97, 97, .8);
    }
  }
  .body {
    width: 100%;
    margin: 10px 0;
  }
  textarea {
    display: inline-block;
    width: 95%;
    // border-radius: 2px;
    // border:1px solid transparent;
    // box-shadow: 0 0 3px rgba(97, 97, 97, .8);
    padding: 10px;
  }
  .auth-code {
    input {
      display: inline-block;
      width: 180px;
      height: 30px;
      margin-right: 20px;
      padding-left: 10px;
      // border-radius: 5px;
      // border: 1px solid transparent;
      // box-shadow: 0 0 3px rgba(97, 97, 97, 0.8);
    }
    img {
      position: absolute;
      height: 50px;
    }
  }
  .submit-but {
    button {
      display: inline-block;
      height: 30px;
      margin: 10px 5px;
      padding: 0 10px;
      // border-radius: 5px;
      border: 1px solid transparent;
      outline: none;
      // box-shadow: 0 0 3px rgba(97, 97, 97, 0.8);
      background-color: #fff;
      cursor: pointer;
      &:active {
        background-color: #ccc;
      }
    }
  }
}
</style>
