<template>
  <div>
    <!-- every day  -->
    <div class="message">
      <h2>来说两句</h2>
      <hr />
      <div class="blogger">
        <p>
          都来了，留下你的印记。
        </p>
         <p>
          关注前后端开发知识。
        </p>
      </div>
    </div>
    <div class="count">留言: 共 {{count}} 条留言</div>
<com-message v-for=" item in replyList" :key="+item.id"
            :reply="item" :addCommentFoucs="addCommentFoucs" />
<add-comment :replyId="replyId" :bid="-1" :clearReply="clearReply" :getData="getData" />

  </div>
</template>

<script>
import ComMessage from '../components/comment/ComMessage.vue';
import AddComment from '../components/comment/AddComment.vue';
import { getComment } from '../api';

export default {
  data() {
    return {
      replyId: {// 要回复的评论消息{id, user_name}
        id: -1,
        user_name: '请输入评论',
      },
      replyList: [{
        id: Number,
        blog_id: Number,
        parent: Number,
        user_name: String,
        comments: String,
        emial: String,
        child: Array,
      }],
      count: 0,
    };
  },
  components: {
    ComMessage,
    AddComment,
  },
  methods: {
    addCommentFoucs(mid) {
      const rInfo = mid;
      rInfo.user_name = `回复：${mid.user_name}`;
      this.replyId = mid;
    },
    clearReply() {
      this.replyId = {
        id: -1,
        user_name: '请输入评论',
      };
    },
    async getData() {
      const result = await getComment(-1);
      // console.log(result);
      if (result.code === 0) {
        this.count = result.data.count;
        this.replyList = result.data.data; // JSON.parse(result.data.data);
      }
    },
  },
  async created() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
.message {
  width: calc(100% - 40);
  border-radius: 5px;
  background-color: #f5f5f5;
  box-shadow: 0 0 8px rgba(97, 97, 97, 0.8);
  padding: 40px;
  .blogger {
    width: 100%;
    margin: 20px 10px;
  }
  p {
    font-size: 14px;
    line-height: 25px;
    color: #333;
  }
}
.count{
  margin:20px 10px 10px 10px;
}

</style>
