<template>
  <div class="comment">
    <div class="headP">
      <span>
        <i class="el-icon-s-custom"></i>
      </span>
    </div>
    <div class="cbody">
      <div class="auther">
        <span class="name">{{reply.user_name}}</span>
        <span class="public">发表于{{reply.utime}}</span>
        <span class="reply">
            <a
            v-on:click="addCommentFoucs({id: reply.id, user_name: reply.user_name})">[回复]</a>
        </span>
      </div>
      <div class="comment-body">
        <div v-html="reply.comments"></div>
      <sub-com-message v-for="item in reply.child" :key="item.id" :reply="item" :addCF="addCF()" />
      </div>
    </div>
    <input  :ref="'cid'+reply.id" type="text" />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import SubComMessage from './SubComMessage.vue';

export default {
  props: {
    reply: Object,
    addCommentFoucs: Function,
  },
  computed: {
    ...mapState(['cId']),
  },
  components: {
    SubComMessage,
  },
  watch: {
    /** 如果点击了最新评论获取焦点 */
    cId(newV) {
      // console.log('watch', newV);
      this.$nextTick(() => {
        const cc = this.$refs[`cid${newV.cid}`];
        if (cc !== 'undefined' && cc) {
          cc.focus();
        }
      });
    },
  },
  methods: {
    addCF() {
      return (name) => {
        const mInfo = {
          id: this.reply.id,
          user_name: name,
          replyInfo: name, // 回复的用户名
        };
        this.addCommentFoucs(mInfo);
      };
    },
    getFous(cid) {
      // console.log('aaa', this.$refs);
      const cc = this.$refs[`cid${cid}`];
      // console.log('bbb', cid, cc);
      if (cc !== 'undefined' && cc) {
        cc.focus();
      }
    },
  },
  mounted() {
    /** 如果点击了最新评论获取焦点 */
    this.$nextTick(() => {
      // console.log('mounted', this.cId.cid);
      this.getFous(this.cId.cid);
      // this.$store.commit('setCId', { cid: null });
    });
  },
};
</script>

<style lang="scss" scoped>
input{ // 如果点击了最新评论获取焦点的 载体
  position:absolute;
  display:inline-block;
  width: 0;
  left:-10px;
}
.comment {
  overflow: hidden;
  width: calc(100% - 40);
  border-radius: 5px;
  background-color: rgba(210, 214, 216, 0.5); // #cbe7eb;
  // box-shadow: 0 0 8px rgba(119, 203, 218, 0.5);
  padding: 10px 10px;
  margin: 15px 0;
  display: flex;
  .headP {
    width: 35px;
    span {
      display: inline-block;
      width: 35px;
      height: 35px;
      line-height: 35px;
      margin-left: -4px;
      background-color: #ddd;
      border-radius: 50%;
      text-align: center;
      i {
        display: inline-block;
        width: 30px;
        height: 30px;
      }
    }
  }
  .cbody {
    flex: 1;
    .auther {
      position: relative;
      height: 45px;
      .name {
        display: inline-block;
        height: 35px;
        line-height: 35px;
        color: #792510;
      }
      .public {
        position: absolute;
        right: 60px;
        display: inline-block;
        height: 35px;
        line-height: 35px;
        color: #909090;
      }
      .reply {
        position: absolute;
        margin: 0 10px;
        top: 8px;
        right: 0;
        a {
          text-decoration: none;
          color: #909090;
          &:hover {
            color: black;
            cursor: pointer;
          }
        }
      }
    }
    .comment-body {
      font-size: 14px;
      div {
        line-height: 22px;
      }
      color: rgb(59, 73, 107);
      .reply-body {
        height: 25px;
        line-height: 25px;
        margin: 5px 0;
        border: 1px solid rgba(119, 203, 218, 0.5);
      }
    }
  }
}
</style>
