<template>
  <div>
    <article-item v-for="item in articleList" :key="+item.id" :art="item" />
    <div>
      <paging :curPage="curPage" :pageSize="pageSize" :total="+total" :toPage="getData" />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import ArticleItem from './ArticleItem.vue';
import Paging from './Paging.vue';
import { getArticle } from '../api';

export default {
  data() {
    return {
      total: 0, // 总记录数
      curPage: 1, // 当前页
      limit: 5, // 数字按钮数
      pageSize: 5, // 每页记录数
      articleList: [
        {
          id: Number,
          title: String,
          date: String,
          content: String,
          views: Number,
          tags: Array,
        },
      ],
    };
  },
  components: {
    ArticleItem,
    Paging,
  },
  computed: {
    ...mapState(['tagId', 'searchVal']),
  },
  methods: {
    async getData(page, pageSize) {
      // condition  { tid: Array, content: String }
      const condition = {};
      if (this.tagId) condition.tid = this.tagId;
      if (this.searchVal) condition.content = this.searchVal;
      //
      const result = await getArticle(page, pageSize, condition);
      this.articleList = result.data; // JSON.parse(result.data);
      this.total = result.count;
      this.curPage = page;
    },
  },
  watch: {
    /** 随机云标签中改变后的监听 */
    tagId() {
      this.getData(1, this.pageSize);
    },
    /** 搜索监听 */
    searchVal() {
      this.getData(1, this.pageSize);
    },
  },
  async created() {
    this.getData(this.curPage, this.pageSize);
  },
};
</script>

