<template>
  <div class="paging">
    <span
      v-for="item in renderPaging()"
      :key="item.key"
      v-on:click="changePage(item.page)"
      :class="item.class"
    >{{item.number}}</span>
  </div>
</template>

<script>
export default {
  props: {
    total: {
      // 总记录数
      type: Number,
      default: 0,
      //   required: true,
    },
    curPage: {
      // 当前页
      type: Number,
      default: 1,
      //   required: true,
    },
    // count: {
    //   type: Number,
    //   default: 5,
    //   //   required: true,
    // }, // 总页数
    limit: {
      type: Number,
      default: 5,
      //   required: true,
    }, // 数字按钮数
    pageSize: {
      type: Number,
      default: 5,
      //   required: true,
    }, // 每页记录数
    toPage: Function,
  },
  data() {
    return {
      list: Array,
    };
  },
  methods: {
    renderPaging() {
      const min = this.getMin();
      const max = this.getMax(min);
      const result = [];
      // 首页、上一页
      const disabled = this.curPage === 1 ? 'disabled' : '';
      if (this.curPage !== 1) {
        result.push({
          key: 'first',
          page: 1,
          number: '首页',
          class: `common first ${disabled}`,
        });
        result.push({
          key: 'prev',
          page: this.curPage - 1,
          number: '上一页',
          class: `common first ${disabled}`,
        });
      }

      // 数字页码
      for (let i = min; i <= max; i += 1) {
        if (i === this.curPage) {
          result.push({
            key: i,
            page: i,
            number: i,
            class: 'number active',
          });
        } else {
          result.push({
            key: i,
            page: i,
            number: i,
            class: 'number',
          });
        }
      }
      // 下一页、尾页
      //   const disabledNext = max >= this.count ? 'disabled' : '';
      if (this.curPage < this.getCount) {
        result.push({
          key: 'next',
          page: this.curPage + 1,
          number: '下一页',
          class: 'common next  ',
        });
        result.push({
          key: 'last',
          page: this.getCount,
          number: '尾页',
          class: 'common next  ',
        });
      }
      return result;
    },
    getPageNumber() {
      return Math.ceil(this.total / this.limit);
    },
    getMin() {
      let min = this.curPage - Math.floor(this.limit / 2);
      if (min < 1) min = 1;
      return min;
    },
    getMax(min) {
      let max = min + this.limit - 1;
      if (max > this.getCount) max = this.getCount;
      if (this.getCount < max) {
        max -= max - this.getCount;
      }
      return max;
    },
    changePage(page) {
      this.toPage(page, this.pageSize, {});
    },
  },
  computed: {
    getCount() {
      return Math.ceil(this.total / this.pageSize);
    },
  },
};
</script>

<style lang="scss" scoped>
.paging {
  height: 35px;
  margin-top: 30px;
  margin: 35px 0;
  font-size: 0;
  /* background-color: cadetblue; */
  .common {
    display: inline-block;
    width: 70px;
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-size: 15px;
    box-sizing: border-box;
    background-color: #fff;
    cursor: pointer;
  }
  .first {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-left: 1px solid rgba(0, 0, 0, 0.2);
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  .previous {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-left: 1px solid rgba(0, 0, 0, 0.2);
  }
  .next {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-left: 1px solid rgba(0, 0, 0, 0.2);
  }
  .last {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    border-right: 1px solid rgba(0, 0, 0, 0.2);
    border-left: 1px solid rgba(0, 0, 0, 0.2);
  }
  .number {
    display: inline-block;
    width: 35px;
    height: 35px;
    line-height: 35px;
    font-size: 15px;
    text-align: center;
    box-sizing: border-box;
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    border-left: 1px solid rgba(0, 0, 0, 0.2);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    background-color: #fff;
    cursor: pointer;
  }
  .active {
    background-color: #444;
    color: #fff;
    cursor: default;
  }
}
</style>
