<template>
  <!-- every day  -->
  <div class="every-day" id="every-day">
    <span class="day" :style="{ color: randomColor }">每日一句正能量</span>
    <p v-html="getContent"></p>
  </div>
</template>
<script>
import { getEveryDay } from '../api';


export default {
  data() {
    return {
      show: true,
      content: '',
      color: {
        red: 0,
        green: 0,
        blue: 0,
        alpha: 1,
      },
    };
  },
  computed: {
    getContent() {
      return this.content;
    },
    randomColor() {
      return this.randomColor2();
    },
  },
  methods: {
    randomColor2() {
      // const red = Math.random() * 255;
      // const green = Math.random() * 255;
      // const blue = Math.random() * 255;
      return `rgb(${this.color.red}, ${this.color.green}, ${this.color.blue})`;
    },
  },
  async created() {
    const result = await getEveryDay();
    this.content = JSON.parse(result.data).content;
    setInterval(() => {
      this.color.red = Math.random() * 255 + 25;
      this.color.green = Math.random() * 255 + 25;
      this.color.blue = Math.random() * 255;
    }, 3000);
  },
};
</script>

<style lang="scss" scoped>
.every-day {
  width: calc(100% - 40);
  // height: 80px;
  border-radius: 5px;
  background-color: #f5f5f5;
  box-shadow: 0 0 8px rgba(97, 97, 97, 0.8);
  padding: 20px;
  & > span {
    display: inline-block;
    width: 100%;
    height: 20px;
    font-size: 12px;
  }
  & > p {
    font-size: 13px;
    height: 20px;
    line-height: 20px;
    color: #333;
  }
}
</style>
