<template>
  <div>
    <!-- blog content -->
    <div class="art-body" id="article">
      <span class="day">当前位置:</span>
      <span class="day">
        <a href="/home/">首页</a>
      </span> >>
      <span class="day">{{tags}}
      </span> >> 正文
      <div class="title">{{title}}</div>
      <div class="info">
        <span class="day">作者: 博主</span>
        <span class="day">
          分类：
          {{tags}}
        </span>
        <span class="day">发布于：{{ctime}}</span>
        <span class="day">浏览： {{views}}</span>
        <span class="day">
          评论：
        </span>
      </div>
      <hr />
      <div class="main-body" v-html="content"></div>
    </div>
    <com-message
      v-for=" item in replyList"
      :key="+item.id"
      :reply="item"
      :addCommentFoucs="addCommentFoucs"
    />
    <add-comment :replyId="replyId" :bid="bid" :clearReply="clearReply" :getData="getCommentData" />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import ComMessage from '../components/comment/ComMessage.vue';
import AddComment from '../components/comment/AddComment.vue';
import { getArt, getComment } from '../api';

export default {
  data() {
    return {
      bid: null,
      title: '',
      content: '',
      ctime: '',
      tags: '',
      views: '',

      replyId: {
        // 要回复的评论消息{id, user_name}
        id: -1,
        user_name: '请输入评论',
      },
      replyList: [
        {
          id: Number,
          blog_id: Number,
          parent: Number,
          user_name: String,
          comments: String,
          emial: String,
          child: Array,
        },
      ],
      count: 0,
    };
  },
  computed: {
    ...mapState(['artId']),
  },
  watch: {
    async artId() {
      await this.getBlog();
      await this.getCommentData();
    },
  },
  components: {
    ComMessage,
    AddComment,
  },
  methods: {
    /** 点击回复时获得焦点 */
    addCommentFoucs(mid) {
      const rInfo = mid;
      rInfo.user_name = `回复：${mid.user_name}`;
      this.replyId = mid;
    },
    clearReply() {
      this.replyId = {
        id: this.artId,
        user_name: '请输入评论',
      };
    },
    /** 读取评论 */
    async getCommentData() {
      console.log();
      const result = await getComment(this.bid);
      if (result.code === 0) {
        this.count = result.data.count;
        this.replyList = result.data.data; // JSON.parse(result.data.data);
      }
    },
    /** 读取正文 */
    async getBlog() {
      const result = await getArt(this.artId); // this.$route.params.id
      const artObj = JSON.parse(result.data);
      this.bid = artObj.id;
      this.title = artObj.title;
      this.content = artObj.content;
      this.ctime = artObj.ctime;
      this.tags = artObj.tags;
      this.views = artObj.views;
    },
  },
  beforeCreate() {
    const bid = this.$route.params.id;
    this.$store.commit('setArtId', { id: bid });
  },
  async created() {
    await this.getBlog();
    await this.getCommentData();
  },
};
</script>

<style lang="scss" scoped>
.art-body {
  position: relative;
  width: calc(100% - 40);
  border-radius: 5px;
  background-color: #f5f5f5;
  box-shadow: 0 0 8px rgba(97, 97, 97, 0.8);
  padding: 20px;
  span {
    margin-right: 5px;
  }
  .title {
    padding: 20px 0px 15px 0px;
    line-height: 30px;
    font-size: 22px;
    // font-weight: 800;
    text-align: left;
  }
  .info {
    position: absolute;
    padding: 10px 0px;
    font-size: 14px;
    right: 29px;
  }
  hr {
    margin-top: 30px;
  }
  .main-body {
    padding: 10px 10px;
    line-height: 25px;
    color: #555;
  }
  a {
    text-decoration: none;
    color: #337ab7;
  }
}
</style>
